<template>
  <div ref="content">
    <b-form @submit.prevent="submit" class="padding-level-1">
      <b-row>
        <b-col cols="12" sm="2" lg="2">
          <b-button variant="primary" class="px-4 required-btn mr-2">
            {{ $t("REQUIREDBUTTON") }}
          </b-button>
          <label class="line-3">
            {{ $t("ACCOUNT") }}
          </label>
        </b-col>
        <b-col cols="12" sm="4" lg="3">
          <b-form-input
            v-model.trim="$v.inputFilter.account.$model"
            :disabled="true"
            class="mb-2"
          >
          </b-form-input>
          <show-errors
            :control="$v.inputFilter.account"
            :field="$t('ACCOUNT')"
          />
        </b-col>
        <b-col cols="12" sm="4" lg="3">
          <b-button
            @click="showAccounts"
            :disabled="isPopUpDisabled"
            variant="primary"
          >
            {{ $t("ACCOUNTSELECTION") }}
          </b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" sm="2" lg="2">
          <b-button variant="primary" class="px-4 required-btn mr-2">
            {{ $t("REQUIREDBUTTON") }}
          </b-button>
          <label class="line-3">
            {{ $t("EXPENSEITEMCODE") }}
          </label>
        </b-col>
        <b-col cols="12" sm="4" lg="3">
          <b-form-input
            :disabled="isPopUpDisabled"
            v-model.trim="$v.inputFilter.expense_item_code.$model"
            maxlength="3"
            oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
          >
          </b-form-input>
          <show-errors
            :control="$v.inputFilter.expense_item_code"
            :field="$t('EXPENSEITEMCODE')"
          />
        </b-col>
        <b-col cols="12" sm="2" lg="2">
          <b-button variant="primary" class="px-4 required-btn mr-2">
            {{ $t("REQUIREDBUTTON") }}
          </b-button>
          <label class="line-3">
            {{ $t("EXPENSEITEMNAME") }}
          </label>
        </b-col>
        <b-col cols="12" sm="4" lg="3">
          <b-form-input
            v-model.trim="$v.inputFilter.expense_item_name.$model"
            maxlength="20"
          >
          </b-form-input>
          <show-errors
            :control="$v.inputFilter.expense_item_name"
            :field="$t('EXPENSEITEMNAME')"
          />
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" sm="2" lg="2">
          <b-button variant="primary" class="px-4 required-btn mr-2">
            {{ $t("REQUIREDBUTTON") }}
          </b-button>
          <label class="line-3">
            {{ $t("USEFLAG") }}
          </label>
        </b-col>
        <b-col cols="12" sm="4" lg="3">
          <b-form-group>
            <b-form-radio-group
              v-model="inputFilter.use_flg"
              :options="useFlagOptions"
              name="radio-inline"
            ></b-form-radio-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <label class="title-fw title-fs mb-2 mt-4">{{
            $t("EXTENDEDITEMSETTINGS")
          }}</label>
          <b-button class="px-4 required-btn mr-2 ml-1">
            {{ $t("INPUTEXPENSEITEM.ANYBUTTON") }}
          </b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 1</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_1.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_1.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 2</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_2.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_2.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 3</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_3.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_3.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 4</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_4.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_4.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 5</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_5.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_5.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="12" sm="6" lg="6">
          <h6 class="title-fw">{{ $t("EXTENSIONITEM") }} 6</h6>
          <b-row>
            <b-col cols="6" sm="4" lg="3">
              <b-form-select
                :plain="true"
                v-model.trim="$v.inputFilter.extension_type_selected_6.$model"
                :options="extensionItemType"
              ></b-form-select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" sm="12" lg="10">
              <b-form-input
                v-model.trim="$v.inputFilter.extention_item_6.$model"
                maxlength="10"
                class="mt-2 mb-2"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <b-button variant="primary" class="px-4" type="submit">
            {{ buttonSelect }}
          </b-button>
        </b-col>
      </b-row>
      <hr />
      <b-row>
        <b-col cols="12">
          <label class="title-fw title-fs mb-1">{{
            $t("EVALUATION_JUDGEMENT_SETTINGS")
          }}</label>
          <b-button class="px-4 required-btn mr-2 ml-1"> - </b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <b-button @click="addEvolutionYear" variant="primary" class="mb-2">
            {{ $t("ADDED_EVALUATION_YEAR") }}
          </b-button>
        </b-col>
      </b-row>
      <b-row class="table-height">
        <b-col xl="12">
          <l-table
            :bordered="false"
            :striped="false"
            :borderless="false"
            :name="'accounts'"
            :hasPagination="false"
            :showPerPage="false"
            :table-data="accountsTableData"
            :fields="accountTableFields"
            :serverSideProcess="true"
            :outlined="true"
            :isBusy="isBusy"
            @onSelect="selectItem"
            @onEdit="onEditYearly"
            @onChecked="onCheckedYear"
            small="small"
            fixed
            responsive="sm"
            sticky-header
          ></l-table>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols="12">
          <b-button
            @click="deleteExpenseItemYearly"
            :disabled="checkedItemsYearly == ''"
            variant="primary"
            class="mb-4"
          >
            {{ $t("DELETE_CHECKED_EVALUTION_YEAR") }}
          </b-button>
        </b-col>
      </b-row>
      <!-- <b-row>
        <b-col cols="12">
          <b-button @click="addEvolutionMonth" variant="primary" class="mb-2">
            {{ $t("ADDED_EVALUATION_MONTH") }}
          </b-button>
        </b-col>
      </b-row>
      <b-row class="table-height">
        <b-col xl="12">
          <l-table
            :bordered="false"
            :striped="false"
            :borderless="false"
            :name="'expenses'"
            :hasPagination="false"
            :showPerPage="false"
            :table-data="expensesTableData"
            :fields="expensesTableFields"
            :serverSideProcess="true"
            :outlined="true"
            :isBusy="isBusy"
            @onSelect2="selectItem2"
            @onEdit="onEditMonthly"
            @onChecked2="onCheckedMonth"
            small="small"
            responsive="sm"
            sticky-header
          ></l-table>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols="12">
          <b-button
            @click="deleteExpenseItemMonthly"
            :disabled="checkedItemsMonthly == ''"
            variant="primary"
            class="mb-4"
          >
            {{ $t("DELETE_CHECKED_EVALUTION_MONTH") }}
          </b-button>
        </b-col>
      </b-row> -->
    </b-form>
    <b-modal
      id="account-modal"
      ref="account-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("ACCOUNTSELECTION") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <div v-if="accountsLoading" class="text-center">
        <i class="fa fa-spinner fa-spin fa-2x" />
      </div>
      <div v-else>
        <b-card class="pop-up-box">
          <b-row>
            <b-col>
              <b-form-group>
                <b-form-radio-group
                  id="radio-group-1"
                  v-model="selectAccount"
                  :options="accountOptions"
                  class="radioControl mb-3 pt-2"
                  size="md"
                  stacked
                ></b-form-radio-group>
              </b-form-group>
            </b-col>
          </b-row>
        </b-card>
        <b-row>
          <b-col cols="12" class="text-center mb-2">
            <b-button :disabled="selectAccount == ''" @click="getAccounts" variant="primary" class="float-right"
              >{{ $t("SELECTEXPENSEITEMS") }}
            </b-button>
          </b-col>
        </b-row>
      </div>
    </b-modal>
  </div>
</template>

<script>
import LTable from "@/components/ListTable";
import {ApiServicesFactory} from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import {Settings} from "luxon";
Settings.defaultLocale = "ja";
import {getDate} from "@/shared/utils";
import i18n from "@/plugins/i18n";
import {required} from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import {mapGetters} from "vuex";
import {endPoint} from "@/shared/endPoint";

const defaultForm = {
  account: "",
  expense_item_code: "",
  expense_item_name: "",
  use_flg: "1",
  extention_item_1: "",
  extention_item_2: "",
  extention_item_3: "",
  extention_item_4: "",
  extention_item_5: "",
  extention_item_6: "",
  extension_type_selected_1: "",
  extension_type_selected_2: "",
  extension_type_selected_3: "",
  extension_type_selected_4: "",
  extension_type_selected_5: "",
  extension_type_selected_6: "",
};
export default {
  name: "ExpenseItemAddEdit",
  components: {LTable, ShowErrors},
  data() {
    return {
      inputFilter: {},
      useFlagOptions: [
        {text: i18n.t("USE"), value: "1"},
        {text: i18n.t("UNUSED"), value: "2"},
      ],
      extensionItemType: [
        {text: i18n.t("TEXT"), value: "01"},
        {text: i18n.t("DIGITAL"), value: "02"},
      ],

      accountOptions: [],
      selectAccount: "",
      accountsLoading: false,
      accountsTableData: [],
      expensesTableData: [],
      isBusy: false,
      selectedItemIdsYearly: [],
      selectedItemIdsMonthly: [],
      checkedItems: [],
      AccountCd: "",
      buttonSelect: this.$t("SAVE"),
      isPopUpDisabled: false,
      expansionItemNo: [],
      checkedItemsYearly: "",
      checkedItemsMonthly: "",
      expenseItemCodeData: [],
    };
  },
  validations: {
    inputFilter: {
      account: {required},
      expense_item_code: {required},
      expense_item_name: {required},
      extention_item_1: {},
      extention_item_2: {},
      extention_item_3: {},
      extention_item_4: {},
      extention_item_5: {},
      extention_item_6: {},
      extension_type_selected_1: {},
      extension_type_selected_2: {},
      extension_type_selected_3: {},
      extension_type_selected_4: {},
      extension_type_selected_5: {},
      extension_type_selected_6: {},
    },
  },
  computed: {
    ...mapGetters({
      masterData: "getMasterData",
    }),
    accountTableFields() {
      return [
        {
          key: "checked",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "set_year",
          label: this.$t("SETYEAR"),
          class: "text-left normal-width",
        },
        {
          key: "industry",
          label: this.$t("INDUSTRY"),
          class: "text-left normal-width",
        },
        {
          key: "enterprise_size",
          label: this.$t("ENTERPRISESIZE"),
          class: "text-left normal-width",
        },
        {
          key: "standard_monthly_fee",
          label: this.$t("STANDARDMONTHLYFEE"),
          class: "text-center  normal-width",
        },
        // {
        //   key: "reduction_upper_limit",
        //   label: this.$t("REDUCTION_UPPERLIMIT"),
        //   class: "text-center normal-width",
        // },
        // {
        //   key: "reduction_lower_limit",
        //   label: this.$t("REDUCTION_LOWERLIMIT"),
        //   class: "text-center normal-width ",
        // },
        {
          key: "industry_average",
          label: this.$t("INDUSTRYAVERAGE"),
          class: "text-center normal-width",
        },
        {
          key: "created_date",
          label: this.$t("CREATEDDATE"),
          class: "text-center normal-width",
        },
        {
          key: "updated_date",
          label: this.$t("UpdateDate"),
          class: "text-center normal-width",
        },
        {
          key: "edit",
          label: "",
          class: "text-center normal-width",
        },
      ];
    },
    expensesTableFields() {
      return [
        {
          key: "checked2",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "set_monthly",
          label: this.$t("SETTINGDATE"),
          class: "text-left normal-width",
        },
        {
          key: "industry",
          label: this.$t("INDUSTRY"),
          class: "text-left normal-width",
        },
        {
          key: "enterprise_size",
          label: this.$t("ENTERPRISESIZE"),
          class: "text-left normal-width",
        },
        {
          key: "sense_of_cost_up",
          label: this.$t("UPPERLIMIT1"),
          class: "text-center normal-width",
        },
        {
          key: "sense_of_cost_low",
          label: this.$t("LOWERLIMIT2"),
          class: "text-center normal-width ",
        },
        {
          key: "created_date",
          label: this.$t("CREATEDDATE"),
          class: "text-center normal-width",
        },
        {
          key: "updated_date",
          label: this.$t("UpdateDate"),
          class: "text-center normal-width",
        },
        {
          key: "edit",
          label: "",
          class: "text-center normal-width",
        },
      ];
    },
  },
  created() {
    this.inputFilter = {...defaultForm};
  },
  mounted() {
    this.getAccountList();
    this.getExpenseData();
    this.setDefaultData();
  },
  methods: {
    /**
     * @description function to set default values from store "getMasterData"
     * @name setDefaultData
     * @param none
     * @return nothing
     */
    setDefaultData() {
      console.log("IIIIIIIII",this.masterData);
      if (this.masterData.expenseMasterData) {
        this.isPopUpDisabled = true;
        this.buttonSelect = this.$t("UPDATE");

        this.inputFilter.account =
          "(" +
          this.masterData.expenseMasterData.item.AccountCode +
          ")" +
          " " +
          this.masterData.expenseMasterData.item.AccountName;
        this.inputFilter.expense_item_code = this.masterData.expenseMasterData.item.ExpenseItemCode;
        this.inputFilter.expense_item_name = this.masterData.expenseMasterData.item.ExpenseItemName;
        this.AccountCd = this.masterData.expenseMasterData.item.AccountCode;
        this.inputFilter.use_flg =
          this.masterData.expenseMasterData.item.UseFlag == true ? "1" : "2";
        ``;
        this.getExpansionItem();
      } else {
        this.isPopUpDisabled = false;
        this.buttonSelect = this.$t("SAVE");
        this.getExpenseItemData();
      }
    },
    /**
     * @description function to get All expense items from master table and assign data to the variable "expenseItemCodeData"
     * @name getExpenseItemData
     * @param none
     * @returns nothing
     */
    async getExpenseItemData() {
      const {data} = await BasicInfoApis.getAPI(
        endPoint.MASTERREADALLMEXPENSEITEM,
        {
          fieldlist: ["*"],
        }
      );
      if (data.success) {
        this.expenseItemCodeData = data.data;
      }
    },
    /**
     * @description function to show accounts modal popup
     * @name ShowAccounts
     * @param none
     * @returns nothing
     */
    showAccounts() {
      this.$refs["account-modal"].show();
    },
    /**
     * @description function to get the selected account and hide the account modal popup
     * @name getAccounts
     * @param none
     * @returns nothing
     */
    getAccounts() {
      console.log("PPPPPPP", this.selectAccount);
      let accountDetails = this._.find(this.accountOptions, {
        value: this.selectAccount,
      });
      this.inputFilter.account =
        "(" + accountDetails.value + ") " + accountDetails.text;
      this.AccountCd = accountDetails.value;
      this.$refs["account-modal"].hide();
    },
    /**
     * @description function to get the checked year items
     * @name onCheckedYear
     * @param items
     * @returns nothing
     */
    onCheckedYear(items) {
      this.checkedItemsYearly = items.item;
    },
    /**
     * @description function to get the checked month items
     * @name onCheckedMonth
     * @param items
     * @returns nothing
     */
    onCheckedMonth(items) {
      this.checkedItemsMonthly = items.item;
    },
    /**
     * @description function to set the selected items Ids yearly
     * @name selectItem
     * @param items
     * @returns nothing
     */
    selectItem(items) {
      this.selectedItemIdsYearly = items;
    },
    /**
     * @description function to set the selected items Ids monthly
     * @name selectItem2
     * @param items
     * @returns nothing
     */
    selectItem2(items) {
      this.selectedItemIdsMonthly = items;
    },
    /**
     * @description function to get the expansion item
     * @name getExpansionItem
     * @param none
     * @returns nothing
     */
    async getExpansionItem() {
      // api call to read the master cost expansion items by account_name_cd and expense_item_cd
      const {data} = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMCOSTEXPANSIONITEM,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "accounts_name_cd",
              value: this.masterData.expenseMasterData.item.AccountCode,
            },
            {
              key: "expense_item_cd",
              value: this.masterData.expenseMasterData.item.ExpenseItemCode,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        let expansionItems = [];
        responseData.map(item => {
          expansionItems.push({
            name: item ? item.expansion_item_nm : "-",
            value: "",
            id: item ? item.cost_expansion_item_no : "-",
            kbn: item ? item.expansion_item_format_kbn : "-",
          });
          this.expansionItemNo.push({
            id: item ? item.cost_expansion_item_no : "-",
          });
        });
        // assigns the expansion items to the each field
        this.inputFilter.extention_item_1 = expansionItems[0]
          ? expansionItems[0].name
          : "";
        this.inputFilter.extention_item_2 = expansionItems[1]
          ? expansionItems[1].name
          : "";
        this.inputFilter.extention_item_3 = expansionItems[2]
          ? expansionItems[2].name
          : "";
        this.inputFilter.extention_item_4 = expansionItems[3]
          ? expansionItems[3].name
          : "";
        this.inputFilter.extention_item_5 = expansionItems[4]
          ? expansionItems[4].name
          : "";
        this.inputFilter.extention_item_6 = expansionItems[5]
          ? expansionItems[5].name
          : "";
        this.inputFilter.extension_type_selected_1 = expansionItems[0]
          ? expansionItems[0].name
            ? expansionItems[0].kbn
            : ""
          : "";
        this.inputFilter.extension_type_selected_2 = expansionItems[1]
          ? expansionItems[1].name
            ? expansionItems[1].kbn
            : ""
          : "";
        this.inputFilter.extension_type_selected_3 = expansionItems[2]
          ? expansionItems[2].name
            ? expansionItems[2].kbn
            : ""
          : "";
        this.inputFilter.extension_type_selected_4 = expansionItems[3]
          ? expansionItems[3].name
            ? expansionItems[3].kbn
            : ""
          : "";
        this.inputFilter.extension_type_selected_5 = expansionItems[4]
          ? expansionItems[4].name
            ? expansionItems[4].kbn
            : ""
          : "";
        this.inputFilter.extension_type_selected_6 = expansionItems[5]
          ? expansionItems[5].name
            ? expansionItems[5].kbn
            : ""
          : "";
      }
    },
    /**
     * @description function to get the master accounts
     * @name getAccountList
     * @param none
     * @returns nothing
     */
    async getAccountList() {
      this.accountsLoading = true;
      let compiledArray = {
        fieldlist: ["*"],
      };
      const {data} = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMACCOUNTSNAME,
        compiledArray
      );
      this.accountsLoading = false;

      if (data.success) {
        let responseData = data.data;

        this.accountOptions = [];
        if (responseData.length) {
          responseData.forEach(data => {
            this.accountOptions.push({
              value: data.accounts_name_cd,
              text: data.accounts_name_nm,
            });
          });
        }
      }
    },
    /**
     * @description function to register the expansion item to the db
     * @name submit
     * @param none
     * @returns nothing
     */
    async submit() {
      this.$v.inputFilter.$touch();
      if (!this.$v.inputFilter.$invalid) {
        //payload
        let accountDetails = this._.find(this.accountOptions, {
          value: this.selectAccount,
        });
        let account_cd = this.AccountCd;
        if (accountDetails) {
          account_cd = accountDetails.value;
        }
        let expenseDetails = this._.find(this.expenseItemCodeData, {
          expense_item_cd: this.inputFilter.expense_item_code,
          accounts_name_cd: this.selectAccount,
        });
        let expense_item_object = {
          expense_item_cd: this.inputFilter.expense_item_code,
          expense_item_nm: this.inputFilter.expense_item_name,
          accounts_name_cd: account_cd,
          create_id: localStorage.getItem("personID"),
          update_id: localStorage.getItem("personID"),
          use_flg: this.inputFilter.use_flg == 1 ? true : false,
        };
        let cost_expansion_item_object = [];
        for (let i = 1; i <= 6; i++) {
          let key1 = `extention_item_${parseInt(i)}`;
          let key2 = `extension_type_selected_${parseInt(i)}`;
          if (this.inputFilter[key1] || this.inputFilter[key2]) {
            let item = {
              expansion_item_nm: this.inputFilter[key1],
              expansion_item_format_kbn: this.inputFilter[key2],
            };
            if (this.expansionItemNo[i - 1]) {
              item.cost_expansion_item_no = this.expansionItemNo[i - 1].id;
            }
            cost_expansion_item_object.push(item);
          }
        }
        let compiledArray = {
          expense_item_cd: this.inputFilter.expense_item_code,
          accounts_name_cd: account_cd,
          expense_item_object: expense_item_object,
          cost_expansion_item_object: cost_expansion_item_object,
        };
        if (this.masterData.expenseMasterData || !expenseDetails) {
          const {data} = await BasicInfoApis.postAPI(
            endPoint.EXPENSEITEM,
            compiledArray
          );
          if (data.success) {
            this.$router.push({ name: "mastermaintenance_list" });
          }
        } else {
          this.$swal({
            width: 590,
            text: i18n.t("ACCOUNTCHECKMESSAGE"),
            type: "warning",
          });
        }
      }
    },
    /**
     * @description function to get the expense item data
     * @name getExpenseData
     * @param none
     * @returns nothing
     */
    async getExpenseData() {
      this.isBusy = true;
      let expense_item_code = "";
      let accounts_name_code = "";
      if (this.masterData.expenseMasterData) {
        expense_item_code = this.masterData.expenseMasterData.item
          .ExpenseItemCode;
        accounts_name_code = this.masterData.expenseMasterData.item
          .AccountCode;
      } else if (this.inputFilter.expense_item_code) {
        expense_item_code = this.inputFilter.expense_item_code;
        accounts_name_code = this.AccountCd;
      }

      let compiledArray = {
        expense_item_cd: expense_item_code,
        accounts_name_cd : accounts_name_code
      };
      // api call to get the expense item by expense_item_cd
      const {data} = await BasicInfoApis.postAPI(
        endPoint.EXPENSEREAD,
        compiledArray
      );
      this.isBusy = false;

      if (data.success) {
        let setYearListObject = data.data.expenseItemSetYListObject
          ? data.data.expenseItemSetYListObject
          : [];
        let setMonthlyListObject = data.data.expenseItemSetMListObject
          ? data.data.expenseItemSetMListObject
          : [];
        let mapData = [];
        if (setYearListObject.length) {
          // iterate through setYearListObject which is coming from response from the api and create an object and pushing to accountsTable data
          setYearListObject.forEach(item => {
            let listData = {
              id: "setYear" + item.expense_item_set_y_no,
              set_year: item.set_year,
              industry: item.industry_nm,
              enterprise_size: this.getIndustry(item.company_size),
              standard_monthly_fee: item.standard_annual_savings
                ? this.filterByCurrency(item.standard_annual_savings)
                : "",
              reduction_upper_limit: item.reduction_range_up
                ? parseInt(item.reduction_range_up)
                : "",
              reduction_lower_limit: item.reduction_range_low
                ? parseInt(item.reduction_range_low)
                : "",
              industry_average: item.cost_industry_average
                ? this.filterByCurrency(item.cost_industry_average)
                : "",
              created_date: getDate(new Date(item.create_dt)),
              updated_date: getDate(new Date(item.update_dt)),
              company_size: item.company_size,
              industryNo: item.industry_no,
              set_y_no: item.expense_item_set_y_no,
            };
            mapData.push(listData);
          });
        }

        this.accountsTableData = mapData;
        mapData = [];
        if (setMonthlyListObject.length) {
          // iterate through setMonthlyListObject which is coming from response from the api and create an object and pushing to expenseTable data
          setMonthlyListObject.forEach(item => {
            let listData = {
              id: "setMonth" + item.expense_item_set_y_no,
              set_monthly: this.getYearMonth(item.set_monthly.toString()),
              industry: item.industry_nm,
              enterprise_size: this.getIndustry(item.company_size),
              standard_monthly_fee: item.standard_annual_savings
                ? this.filterByCurrency(item.standard_annual_savings)
                : "",
              sense_of_cost_up: item.sense_of_cost_up
                ? this.filterByCurrency(item.sense_of_cost_up)
                : "",
              sense_of_cost_low: item.sense_of_cost_low
                ? this.filterByCurrency(item.sense_of_cost_low)
                : "",
              industry_average: item.cost_industry_average
                ? parseInt(item.cost_industry_average)
                : "",
              created_date: getDate(new Date(item.create_dt)),
              updated_date: getDate(new Date(item.update_dt)),
              company_size: item.company_size,
              industryNo: item.industry_no,
              set_y_no: item.expense_item_set_y_no,
              yearMonth: item.set_monthly.toString(),
            };
            mapData.push(listData);
          });
        }

        this.expensesTableData = mapData;
      }
    },
    /**
     * @description function to delete the expense item yearly with confirmation popup
     * @name deleteExpenseItemYearly
     * @param none
     * @returns nothing
     */
    async deleteExpenseItemYearly() {
      // display delete confirmation popup
      this.$swal({
        text: i18n.t("DELETE_CHECKED_EVALUTION_YEAR_CONFIRM"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("DELETE"),
        cancelButtonText: i18n.t("CANCEL"),
        cancelButtonColor: "#062854",
        cancelButtonClass: "btn-primary",
      }).then(async result => {
        // if user click on ok, delete the itemfrom the db by passing expense item ids
        if (result.value) {
          let expenseIds = Number(
            this.checkedItemsYearly.id.toString().substring(7)
          );
          let compiledArray = {
            conditions: [
              {
                key: "expense_item_set_y_no",
                value: expenseIds,
              },
            ],
            update_id: localStorage.getItem("personID"),
          };
          const {data} = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMEXPENSEITEMSETY,
            compiledArray
          );

          if (data.success) {
            // reload the expense items table
            this.getExpenseData();
          } else {
            // if api response throws an error display the swal popup
            this.$swal({type: "error", text: i18n.t("NOTDELETED")});
          }
        }
      });
    },
    /**
     * @description function to delete the expense item monthly with confirmation popup
     * @name deleteExpenseItemMonthly
     * @param none
     * @returns nothing
     */
    async deleteExpenseItemMonthly() {
      // display delete confirmation popup
      this.$swal({
        text: i18n.t("DELETE_CHECKED_EVALUTION_MONTH_CONFIRM"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("DELETE"),
        cancelButtonText: i18n.t("CANCEL"),
        cancelButtonColor: "#062854",
        cancelButtonClass: "btn-primary",
      }).then(async result => {
        // if user click on ok, delete the itemfrom the db by passing expense item ids
        if (result.value) {
          let expenseIds = Number(
            this.checkedItemsMonthly.id.toString().substring(8)
          );

          let compiledArray = {
            conditions: [
              {
                key: "expense_item_set_y_no",
                value: expenseIds,
              },
            ],
            update_id: localStorage.getItem("personID"),
          };
          const {data} = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMEXPENSEITEMSETM,
            compiledArray
          );

          if (data.success) {
            // reload the expense items table
            this.getExpenseData();
          } else {
            // if api response throws an error display the swal popup
            this.$swal({type: "error", text: i18n.t("NOTDELETED")});
          }
        }
      });
    },
    /**
     * @description function to format the year and month by giving number
     * @name getYearMonth
     * @param item
     * @returns data
     */
    getYearMonth(item) {
      let data = "";
      data += item.slice(0, 4) + "-";
      if (item.slice(4, 6).length == 1) {
        data += "0" + item.slice(4, 6); // if month is one digit append '0' prefix
      } else {
        data += item.slice(4, 6);
      }
      return data;
    },
    /**
     * @description function to get the company type string based on the company size type
     * @name getIndustry
     * @param company_size
     * @returns companytype
     */
    getIndustry(company_size) {
      let companytype = "";
      if (company_size == 1) {
        //
        companytype = i18n.t("SmallIndustry");
      } else if (company_size == 2) {
        companytype = i18n.t("MediumIndustry");
      } else if (company_size == 3) {
        companytype = i18n.t("BigIndustry");
      } else {
        companytype = "";
      }
      return companytype;
    },
    /**
     * @description function to format the currency in japanese currency format
     * @name filterByCurrency
     * @param value
     * @returns currency formatted string
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to add the border to the last element
     * @name rowClass
     * @param item
     * @param type
     * @returns string 'class name in css'
     */
    rowClass(item, type) {
      if (!item || type !== "row") return;
      if (item.last_element) return "t-bottom-border";
    },
    /**
     * @description function to add evalution year to the store and navigate to judgement setting year
     * @name addEvolutionYear
     * @param none
     * @returns nothing
     */
    addEvolutionYear() {
      this.$store.dispatch("updateYMData", {
        AccountCd: this.AccountCd,
        AccountCode: this.inputFilter.account,
        ExpenseCode: this.inputFilter.expense_item_code,
        ExpenseName: this.inputFilter.expense_item_name,
        YearData: null,
        MonthData: null,
      });
      this.$router.push({ name: "master_maintanence_judgement_setting_year" });
    },
    /**
     * @description function to edit the yearly data by updating to the store and navigating to the judgement setting year
     * @name onEditYearly
     * @param item
     * @returns nothing
     */
    onEditYearly(item) {
      this.$store.dispatch("updateYMData", {
        AccountCd: this.AccountCd,
        AccountCode: this.inputFilter.account,
        ExpenseCode: this.inputFilter.expense_item_code,
        ExpenseName: this.inputFilter.expense_item_name,
        YearData: item,
        MonthData: null,
      });
      // this.$router.push({ name: "master_maintanence_judgement_setting_year" });
      this.$router.push({ name: "master_maintanence_judgement_setting_year_update" });
    },
    /**
     * @description function to edit the monthly data by updating to the store and navigating to the judgement setting month
     * @name onEditMonthly
     * @param item
     * @returns nothing
     */
    onEditMonthly(item) {
      this.$store.dispatch("updateYMData", {
        AccountCode: this.inputFilter.account,
        ExpenseCode: this.inputFilter.expense_item_code,
        ExpenseName: this.inputFilter.expense_item_name,
        YearData: null,
        MonthData: item,
      });
      this.$router.push({ name: "master_maintanence_judgement_setting_month" });
    },
    /**
     * @description function to add evalution month to the store and navigate to judgement setting month
     * @name addEvolutionMonth
     * @param none
     * @returns nothing
     */
    addEvolutionMonth() {
      this.$store.dispatch("updateYMData", {
        AccountCd: this.AccountCd,
        AccountCode: this.inputFilter.account,
        ExpenseCode: this.inputFilter.expense_item_code,
        ExpenseName: this.inputFilter.expense_item_name,
        YearData: null,
        MonthData: null,
      });
      this.$router.push({ name: "master_maintanence_judgement_setting_month" });
    },
  },
};
</script>
